import React from 'react';
import {authorize} from "../../../Services/Authorization";
import {getEvents} from "../../../Services/Database";


export default class ByTypeReport extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            events: []
        };

        authorize()
            .then(() => getEvents())
            .then(events => this.setState({...this.state, events}));
    }

    render() {
        const {events} = this.state;

        const investments = events.reduce((result, e) => {
            result[e.investmentId] = result[e.investmentId] || {type: '', invested: 0, commission: 0, received: 0};
            switch (e.type) {
                case 'Open':
                    result[e.investmentId].type = e.data.investmentType;
                    result[e.investmentId].invested += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    break;
                case "Add":
                    result[e.investmentId].invested += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    break;
                case "Profit":
                case "Withdraw":
                case "Close":
                    result[e.investmentId].received += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    break;
            }
            return result;
        }, {});

        const byType = Object.keys(investments)
            .map(key => investments[key])
            .reduce((result, investment) => {
                result[investment.type] = result[investment.type] || {invested: 0, commission: 0, received: 0};
                result[investment.type].invested += investment.invested;
                result[investment.type].commission += investment.commission;
                result[investment.type].received += investment.received;
                return result;
            }, {});
        return (
            <div>
                <table>
                    <thead>
                    <tr>
                        <th>Type</th>
                        <th>Invested Total</th>
                        <th>Commission Total</th>
                        <th>Received</th>
                    </tr>
                    </thead>
                    <tbody>
                    {Object.keys(byType).map(type => (
                        <tr>
                            <td>{type}</td>
                            <td>{byType[type].invested}</td>
                            <td>{byType[type].commission}</td>
                            <td>{byType[type].received}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
        )
    }
}