import React from 'react';
import formStyles from "../../../CommonStyles/form.scss";

const defaultData = {
    amount:0,
    commission:0,
    comment: ''
};

export default ({onDataFieldChanged, data}) => {
    const {amount, commission, comment} = {...defaultData, ...data};
    return (<div>
        <div className={formStyles.field}>
            <label htmlFor="amount" className={formStyles.label}>Amount</label>
            <input id="amount" type="number" className={formStyles.input} value={amount}
                   onChange={e => onDataFieldChanged('amount', +e.target.value)}/>
        </div>
        <div className={formStyles.field}>
            <label className={formStyles.label} htmlFor="commission">Commission</label>
            <input id="commission" className={formStyles.input} type="number" 
                   onChange={e => onDataFieldChanged("commission", +e.target.value)}
                   value={commission}/>
        </div>
        <div className={formStyles.field}>
            <label className={formStyles.label} htmlFor="comment">Comment</label>
            <input id="comment" className={formStyles.input}
                   onChange={e => onDataFieldChanged("comment", e.target.value)}
                   value={comment}/>
        </div>
    </div>);
}