import React from 'react';
import {authorize} from "../../../Services/Authorization";
import {getEvents} from "../../../Services/Database";
import {Link} from "react-router-dom";


export default class AllInvestments extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            events: [],
            filters: {
                onlyOpen: true
            }
        };

        authorize()
            .then(() => getEvents())
            .then(events => this.setState({...this.state, events}));

        this.filter = this.filter.bind(this);
    }

    filter(investments) {
        let result = {};
        if (this.state.filters.onlyOpen) {
            for (let id of Object.keys(investments)) {
                if (!investments[id].isClosed) {
                    result[id] = investments[id]
                }
            }
        }
        return result;
    }

    render() {
        const {events} = this.state;

        let investments = events.reduce((result, e) => {
            result[e.investmentId] = result[e.investmentId] || {type: '', invested: 0, commission: 0, received: 0, balance: 0};
            switch (e.type) {
                case 'Open':
                    result[e.investmentId].type = e.data.investmentType;
                    result[e.investmentId].invested += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    result[e.investmentId].name = e.data.name;
                    result[e.investmentId].balance += e.data.amount || 0;
                    break;
                case "Add":
                    result[e.investmentId].invested += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    result[e.investmentId].balance += e.data.amount || 0;
                    break;
                case "Profit":
                    result[e.investmentId].received += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    break;
                case "Withdraw":
                    result[e.investmentId].received += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    result[e.investmentId].balance -= e.data.amount || 0;
                    break;
                case "Close":
                    result[e.investmentId].received += e.data.amount || 0;
                    result[e.investmentId].commission += e.data.commission || 0;
                    result[e.investmentId].isClosed = true;
                    result[e.investmentId].balance -= e.data.amount || 0;
                    break;
            }
            return result;
        }, {});

        investments = this.filter(investments);

        return (
            <div>
                <table>
                    <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                        <th>Invested Total</th>
                        <th>Commission Total</th>
                        <th>Received</th>
                        <th>Balance</th>
                    </tr>
                    </thead>
                    <tbody>
                    {Object.keys(investments).map(id => (
                        <tr key={id}>
                            <td><Link to={`/events/by-investment/${id}`}>{id}</Link></td>
                            <td>{investments[id].name}</td>
                            <td>{investments[id].invested}</td>
                            <td>{investments[id].commission}</td>
                            <td>{investments[id].received}</td>
                            <td>{investments[id].balance}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
        )
    }
}