import React from 'react';
import {authorize} from "../../../Services/Authorization";
import {getEvents} from "../../../Services/Database";


export default class EventsByInvestment extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            events: []
        };

        authorize()
            .then(() => getEvents())
            .then(events => this.setState({...this.state, events}));
    }

    render() {
        let {events} = this.state;
        const {investmentId} = this.props.match.params;

        events = events.filter(e => e.investmentId === investmentId);

        return (
            <div>
                <table>
                    <thead>
                    <tr>
                        <th>Type</th>
                        <th>Date</th>
                        <th>Data</th>
                    </tr>
                    </thead>
                    <tbody>
                    {events.map((e, index) => (
                        <tr key={index}>
                            <td>{e.type}</td>
                            <td>{e.date}</td>
                            <td>{JSON.stringify(e.data)}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
        )
    }
}