import React from 'react';
import {getSettings, setSettings} from "../../Services/Settings";
import formStyles from "../../CommonStyles/form.scss";

class Settings extends React.Component {
    constructor(props){
        super(props);
        
        this.state = getSettings();

        this.onClientIdChanged = this.onClientIdChanged.bind(this);
        this.onClientSecretChanged = this.onClientSecretChanged.bind(this);
        this.onDatabaseSheetIdentifierChanged = this.onDatabaseSheetIdentifierChanged.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onReset = this.onReset.bind(this);
    }
    
    onClientIdChanged(event){
        this.setState({clientId: event.target.value});
    }
    
    onClientSecretChanged(event){
        this.setState({clientSecret: event.target.value});
    }

    onDatabaseSheetIdentifierChanged(event){
        this.setState({databaseSheetIdentifier: event.target.value});
    }

    onSubmit(e){
        e.preventDefault();
        setSettings(this.state);
    }

    onReset(){
        this.setState(getSettings());
    }
    
    render(){
        const {clientId, clientSecret, databaseSheetIdentifier} = this.state;
        return (
            <form onSubmit={this.onSubmit} className={formStyles.formContainer}>
                <div className={formStyles.field}>
                    <label className={formStyles.label} htmlFor="clientId"> 
                        Google client id: 
                    </label>
                    <input className={formStyles.input} name="clientId" type="text" value={clientId} onChange={this.onClientIdChanged}/>
                </div>
                <div className={formStyles.field}>
                    <label className={formStyles.label} htmlFor="clientSecret"> 
                        Google client secret: 
                    </label>
                    <input className={formStyles.input} id="clientSecret" type="text" value={clientSecret} onChange={this.onClientSecretChanged}/>
                </div>
                <div className={formStyles.field}>
                    <label className={formStyles.label} htmlFor="databaseSheetIdentifier">
                        Database sheet identifier:
                    </label>
                    <input className={formStyles.input} 
                           id="databaseSheetIdentifier" 
                           type="text" 
                           value={databaseSheetIdentifier} 
                           onChange={this.onDatabaseSheetIdentifierChanged}/>
                </div>
                <div className={formStyles.buttonGroup}>
                    <button className={formStyles.button} type="submit">Save settings</button>
                    <button className={formStyles.button} onClick={this.onReset}>Reset</button>
                </div>
            </form>
        )
    }
}

export default Settings;