import React from 'react';
import formStyles from "../../../CommonStyles/form.scss";

const defaultData = {
    name: '',
    description: '',
    amount: 0,
    commission: 0
};

export default function OpenInvestmentForm({onDataFieldChanged, data}) {
    const {name, description, amount, commission, investmentType} = {...defaultData, ...data};
    return (
        <div>
            <div className={formStyles.field}>
                <label htmlFor="investmentId" className={formStyles.label}>Investition Type</label>
                <select id="investmentId"
                        onChange={e => onDataFieldChanged("investmentType", e.target.value)}
                        value={investmentType}
                        className={formStyles.input}>
                    <option value="Poker">Poker</option>
                    <option value="Bonds">Bonds</option>
                </select>
            </div>
            <div className={formStyles.field}>
                <label htmlFor="name" className={formStyles.label}>Name</label>
                <input id="name" className={formStyles.input}
                       onChange={e => onDataFieldChanged("name", e.target.value)} value={name}/>
            </div>
            <div className={formStyles.field}>
                <label htmlFor="description" className={formStyles.label}>
                    Description
                </label>
                <textarea id="description" className={formStyles.input}
                          onChange={e => onDataFieldChanged("description", e.target.value)} value={description}/>
            </div>
            <div className={formStyles.field}>
                <label className={formStyles.label} htmlFor="amount">Amoun</label>
                <input id="amount" className={formStyles.input} type="number"
                       onChange={e => onDataFieldChanged("amount", +e.target.value)} value={amount}/>
            </div>
            <div className={formStyles.field}>
                <label className={formStyles.label} htmlFor="commission">Commission</label>
                    <input id="commission" className={formStyles.input} type="number" onChange={e => onDataFieldChanged("commission", +e.target.value)}
                           value={commission}/>
            </div>
        </div>
    )
}

