import {getSettings} from './Settings.js';

// Array of API discovery doc URLs for APIs used by the quickstart
const DISCOVERY_DOCS = ["https://sheets.googleapis.com/$discovery/rest?version=v4"];

// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
const SCOPES = "https://www.googleapis.com/auth/spreadsheets";

/**
 *  called to load the auth2 library and API client library.
 */
function initialize() {
    return waitGoogleApi()
        .then(loadGAPI)
        .then(initClient,(error) => {
            console.error(JSON.stringify(error));
            throw(error);
        });
}

function loadGAPI() {
    return new Promise(resolve => window.gapi.load('client:auth2', resolve));
}

function waitGoogleApi() {
    let f = (cb) => {
        window.gapi ? cb() : setTimeout(() => f(cb), 500);
    };

    return new Promise(f);
}

/**
 *  Initializes the API client library and sets up sign-in state
 *  listeners.
 */
function initClient() {
    const {clientId, clientSecret} = getSettings();

    return window.gapi.client.init({
        apiKey: clientSecret,
        clientId: clientId,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES
    });
}

function getIsSignInListener() {
    return window.gapi.auth2.getAuthInstance().isSignedIn;
}

/**
 *  Sign in the user upon button click.
 */
function signIn(event) {
    return initialize().then(() => {
        console.log("google api initialized successfully");
        return gapi.auth2.getAuthInstance().signIn();
    })
}

function getValues(spreadsheetId, sheetName, rowStart, rowEnd) {
    return window.gapi.client.sheets.spreadsheets.values.get({
        spreadsheetId: spreadsheetId,
        range: `${sheetName}!A${rowStart}:Z${rowEnd}`,
    })
}

function appendRow(spreadsheetId, sheetName, row) {
    return window.gapi.client.sheets.spreadsheets.values.append({
        spreadsheetId,
        range: `${sheetName}!A1:A${row.length}`,
        valueInputOption: 'RAW',
        resource: {
            values: [row]
        }
    });
}

export {
    signIn,
    getValues,
    appendRow
}