const CLIENT_ID_KEY = 'CLIENT_ID_KEY';
const CLIENT_SECRET_KEY = 'CLIENT_SECRET_KEY';
const DATABASE_SHEET_IDENTIFIER_KEY = 'DATABASE_SHEET_IDENTIFIER_KEY';

export function getSettings(){
    return {
        clientId: window.localStorage.getItem(CLIENT_ID_KEY) || '',
        clientSecret: window.localStorage.getItem(CLIENT_SECRET_KEY) || '',
        databaseSheetIdentifier: window.localStorage.getItem(DATABASE_SHEET_IDENTIFIER_KEY) ||''
    }
}

export function setSettings({clientId, clientSecret, databaseSheetIdentifier}) {
    window.localStorage.setItem(CLIENT_ID_KEY, clientId);
    window.localStorage.setItem(CLIENT_SECRET_KEY, clientSecret);
    window.localStorage.setItem(DATABASE_SHEET_IDENTIFIER_KEY, databaseSheetIdentifier);
}
