import React from 'react';
import {getEvents, addEvent} from '../../Services/Database';
import formStyles from '../../CommonStyles/form.scss';
import OpenInvestmentForm from "./OpenInvestmentForm/OpenInvestmentForm";
import {generateRandomString} from "../../Utilities/random";
import MoneyForm from "./MoneyForm/MoneyForm";
import {authorize} from "../../Services/Authorization";

const defaultState = {
    type: '',
    data: {},
    date: new Date().toJSON().substring(0, 10),
    events: []
};

class AddEvent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {...defaultState};

        this.onTypeChanged = this.onTypeChanged.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.renderMainForm = this.renderMainForm.bind(this);
        this.onDataFieldChanged = this.onDataFieldChanged.bind(this);
        this.onDateChanged = this.onDateChanged.bind(this);
        this.onInvestmentIdChanged = this.onInvestmentIdChanged.bind(this);

        authorize()
            .then(x => getEvents())
            .then(events => events.filter(x => x.type === "Open"))
            .then(events => this.setState({...this.state, events}));

    }

    onInvestmentIdChanged(e) {
        this.setState({...this.state, investmentId: e.target.value});
    }

    onTypeChanged(e) {
        const type = e.target.value;

        let newState = {...this.state, type: e.target.value, data: {}};
        if (type === 'Open') {
            newState.investmentId = generateRandomString();
            newState.data.investmentType = 'Poker';
        }
        this.setState(newState);
    }

    onSubmit(e) {
        e.stopPropagation();
        e.preventDefault();

        addEvent({...this.state})
            .then(response => {
                    console.log(response);
                    this.setState({
                        ...defaultState, events: this.state.events, messageBox: {
                            message: "successfully added",
                            type: "Success"
                        }
                    });
                    // TODO: move messages to a separate component
                    setTimeout(() => this.setState({...this.state, messageBox: null}), 5000);
                },
                error => {
                    console.error(error);
                    this.setState({
                        ...this.state, messageBox: {
                            message: "something went wrong; more details in console!",
                            type: "Error"
                        }
                    });

                    setTimeout(() => this.setState({...this.state, messageBox: null}), 5000);
                });
    }

    onDataFieldChanged(name, value) {
        let newData = {...this.state.data};
        newData[name] = value;
        this.setState({...this.state, data: newData});
    }

    onDateChanged(e) {
        this.setState({...this.state, date: e.target.value});
    }

    renderMainForm() {
        switch (this.state.type) {
            case "Open":
                return <OpenInvestmentForm onDataFieldChanged={this.onDataFieldChanged} data={this.state.data}/>;
            case "Profit":
            case "Withdraw":
            case "Close":
            case "Add":
                return <MoneyForm onDataFieldChanged={this.onDataFieldChanged} data={this.state.data}/>;
            default:
                return null;
        }
    }

    render() {
        const {type, date, investmentId, events, messageBox} = this.state;
        return (<div>
            {messageBox && <div>{messageBox.message}</div>}
            <form className={formStyles.formContainer}>
                <div className={formStyles.field}>
                    <label htmlFor="type" className={formStyles.label}>Event type</label>
                    <select id="type" onChange={this.onTypeChanged} value={type}
                            className={formStyles.input}>
                        <option>Please Select</option>
                        <option value="Open">Open</option>
                        <option value="Add">Add money</option>
                        <option value="Profit">Receive profit</option>
                        <option value="Withdraw">Partially withdraw</option>
                        <option value="Close">Close</option>
                    </select>
                </div>
                {type !== "Open" &&
                <div className={formStyles.field}>
                    <label htmlFor="investmentId" className={formStyles.label}>Investment</label>
                    <select id="investmentId" className={formStyles.input} value={investmentId}
                            onChange={this.onInvestmentIdChanged}>
                        <option>Please select</option>
                        {
                            events.map(x => <option key={x.investmentId} value={x.investmentId}>{x.data.name}</option>)
                        }
                    </select>
                </div>
                }
                <div className={formStyles.field}>
                    <label htmlFor="date" className={formStyles.label}>Date</label>
                    <input id="date" type="date" className={formStyles.input} value={date}
                           onChange={this.onDateChanged}/>
                </div>
                {this.renderMainForm()}
                <div className={formStyles.buttonGroup}>
                    <button onClick={this.onSubmit} className={formStyles.button}>Add event</button>
                </div>
            </form>
        </div>);
    }
}

export default AddEvent;