import {getSettings} from "./Settings";
import {getValues, appendRow} from "./GoogleApi";

const eventsSheetName = 'events';

const emptyProcessor = x => x;
const databaseColumns = [
    {
        key: 'investmentId',
        toDbProcessor: emptyProcessor,
        fromDbProcessor: emptyProcessor
    },
    {
        key: 'type',
        toDbProcessor: emptyProcessor,
        fromDbProcessor: emptyProcessor
    },
    {
        key: 'date',
        toDbProcessor: emptyProcessor,
        fromDbProcessor: emptyProcessor
    },
    {
        key: 'data',
        toDbProcessor: JSON.stringify,
        fromDbProcessor: JSON.parse
    }];

function getEvents(rowStart = 1, rowEnd = 1000) {
    const {databaseSheetIdentifier} = getSettings();

    return getValues(databaseSheetIdentifier, eventsSheetName, rowStart, rowEnd)
        .then(response=>response.result.values)
        .then(rows=>rows.map(mapDatabaseRowToEvent));
}

function addEvent(event) {
    const {databaseSheetIdentifier} = getSettings();
    const databaseRow = mapEventToDatabaseRow(event);

    return appendRow(databaseSheetIdentifier, eventsSheetName, databaseRow);
}

function mapEventToDatabaseRow(event) {
    return databaseColumns.map(column => column.toDbProcessor(event[column.key]));
}

function mapDatabaseRowToEvent(databaseRow) {
    let result = {};
    for (let i = 0; i < databaseColumns.length; i++) {
        const {key, fromDbProcessor} = databaseColumns[i];
        result[key] = fromDbProcessor(databaseRow[i]);
    }
    return result;
}


export {
    getEvents,
    addEvent
}
