import React from 'react';
import Settings from './Components/settings/settings';
import {BrowserRouter as Router, Route} from "react-router-dom";
import Menu from './Components/menu/menu.js';
import AddEvent from './Components/AddEvent/AddEvent';
import ByTypeReport from "./Components/Reporting/ByTypeReport/ByTypeReport";
import AllInvestments from "./Components/Reporting/AllInvestments/AllInvestments";
import EventsByInvestment from "./Components/events/by-investment/EventsByInvestment";

export default () => (
    <Router>
        <div>
            <Menu/>

            <Route path="/" exact redirectTo="/settings"/>
            <Route path="/settings/" component={Settings}/>
            <Route path="/events/add" component={AddEvent}/>
            <Route path="/events/by-investment/:investmentId" component={EventsByInvestment}/>
            <Route path="/reporting/by-type" component={ByTypeReport}/>
            <Route path="/reporting/all" component={AllInvestments}/>
        </div>
    </Router>
);